.under-construction-container {
    position: relative;
    height: 100vh;
    width: 100vw;
  }
  
  .construction-text {
    position: absolute;
    top: 10%;
    width: 100%;
    text-align: center;
    font-size: 2rem;
    color: white;
    z-index: 1;
    text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  }
  